#wrapper {
  padding-left: 245px;
  transition: all 0.4s ease 0s;
}

#sidebar-wrapper {
  margin-left: -235px;
  left: 328px;
  width: 132px;
  position: absolute;
  height: 50%;
  z-index: 1000;
  transition: all 0.4s ease 0s;
}
#page-content-wrapper {
  width: 100%;
  border-left: 1px solid #444;
}

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 245px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.topic-link{
    color: rgba(0,0,0,0.65);
    text-decoration: none;
    font-weight: 600;
    border-radius: 4px;
}
.topic-active{
  color: #1a73e8;
  font-weight: 700;
}
footer{
  padding-top: 10px;
    padding-bottom: 0px;
}
.topic-link:hover{
  color:#1a73e8;
}
@media (max-width:767px) {
  #wrapper {
    padding-left: 0;
  }
  #sidebar-wrapper {
    left: 0;
  }
  #wrapper.active {
    position: relative;
    left: 250px;
  }
  #wrapper.active #sidebar-wrapper {
    left: 250px;
    width: 250px;
    transition: all 0.4s ease 0s;
  }
  #page-content-wrapper {
    border-left: none;
  }
  
}

@media (max-width:1024px){
  #sidebar-wrapper {
    margin-left: -277px
  }
} 

@media screen and (max-width: 1650px) and (min-width: 1490px){
#sidebar-wrapper {
    margin-left: -203px;
}
}
